@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, ::after, ::before {
  box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

.bg-deepcolor {
  background-color: #AA0601; }

.bg-lightcolor {
  background-color: #AA06010D; }

.d-flex {
  display: flex; }

.jc-center {
  justify-content: center; }

.jc-between {
  justify-content: space-between; }

.al-center {
  align-items: center; }

.color-primary {
  color: #650300; }

.text-white {
  color: white; }

.intro, .classic, .recommend, .form {
  padding-top: 60px;
  padding-bottom: 80px; }

.container {
  max-width: 1140px;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 auto; }

@media (max-width: 992px) {
  .container {
    max-width: 768px;
    padding: 0 39px; }
  .intro, .classic, .recommend, .form {
    padding-top: 40px;
    padding-bottom: 56px; } }

@media (max-width: 767px) {
  .container {
    max-width: 375px;
    padding: 0 15px; }
  .intro, .classic, .recommend, .form {
    padding-top: 32px;
    padding-bottom: 40px; } }

/* 首頁 */
.navbar {
  /* height: 108px; */
  padding: 30px 88px; }

.logo {
  width: 80px;
  height: 49px;
  background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/logo.png?raw=true");
  display: block;
  /* 隱藏文字 */
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap; }

.header-btn {
  line-height: 30px;
  flex-wrap: wrap; }

/* .header-btn li{
  margin-right: 64px;
} */
.header-btn li a {
  text-decoration: none;
  color: #ffffff;
  padding-left: 64px;
  font-size: 20px; }

.header-btn li a:hover {
  font-size: 22px;
  font-weight: bold; }

.header-page {
  background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner.png?raw=true");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover; }

.header-info {
  align-items: center;
  padding-top: 384px;
  padding-bottom: 356px;
  text-align: end; }

.header-page span {
  font-size: 36px;
  line-height: 54px; }

.header-info h3 {
  font-size: 64px;
  line-height: 96px;
  text-align: end; }

@media (max-width: 992px) {
  .header {
    margin-bottom: 0px; }
  .navbar {
    padding: 30px 30px; }
  .header-btn li {
    margin-right: 40px; }
  .header-btn li:nth-child(4) {
    margin-right: 0px; }
  .header-page h3 {
    font-size: 32px;
    text-align: center; }
  .header-page h3 span {
    font-size: 20px; } }

@media (max-width: 767px) {
  .header {
    padding-top: 10px; }
  .navbar {
    width: 100%;
    display: block;
    padding: 0px; }
  .logo {
    margin-bottom: 10px;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center; }
  .header-btn {
    width: 100%; }
  .header-btn li {
    width: 50%;
    margin-right: 0;
    border: 1px solid #ffffff; } }

/* 專業的心 */
.intro h4 {
  text-align: center;
  padding-bottom: 48.5px;
  font-size: 48px;
  line-height: 72px; }

.intro-info {
  flex-wrap: wrap; }

.intro-card {
  width: 20%;
  margin: 48px 15px; }

.intro-card img {
  height: 84px;
  margin: 27px auto; }

.intro-card h5 {
  text-align: center;
  margin: 8px auto;
  font-size: 24px;
  line-height: 36px; }

.intro-card p {
  width: 255px;
  height: 72px;
  color: #000;
  font-size: 16px;
  line-height: 24px;
  flex-wrap: wrap;
  /* 想請問助教這裡的字要怎麼置中? */ }

@media (max-width: 992px) {
  .intro h4 {
    font-size: 32px;
    line-height: 48px;
    padding-bottom: 28px; }
  .intro-card {
    width: 40%;
    margin-bottom: 40px;
    flex-wrap: wrap; } }

@media (max-width: 767px) {
  .intro h4 {
    font-size: 24px; }
  .intro-card {
    width: 100%; } }

/* 經典鏡框 */
.classic h4 {
  text-align: center;
  font-size: 48px;
  line-height: 72px; }

.brand li img {
  width: 60%;
  padding: 16px 2%; }

.brand li:nth-child(2) {
  flex-direction: row-reverse;
  justify-content: flex-start; }

@media (max-width: 992px) {
  .classic h4 {
    font-size: 32px;
    line-height: 48px; }
  .brand li:nth-child(2) {
    flex-direction: row; } }

@media (max-width: 767px) {
  .classic h4 {
    font-size: 32px;
    line-height: 48px; }
  .brand img {
    max-width: 100%;
    margin: 0 auto;
    position: absolute; }
  .brand h4 {
    position: relative;
    padding: 66px 15px; }
  /* 助教，這裡文字也無法置中啦qq，而且一直縮小之後就會出現x軸了(拜託不要把我掛在牆上鞭屍) */ }

/* 聯名設計框 */
.joint-name {
  background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/bg.png?raw=true");
  background-position: center center;
  background-size: cover;
  padding-top: 60px;
  padding-bottom: 80px; }

.joint-name h3 {
  color: #ffffff;
  font-size: 64px;
  line-height: 96px;
  text-align: center;
  margin-bottom: 40px; }

.joint-info li {
  width: 49%;
  position: relative; }

.double-a h2 a {
  display: block;
  max-width: 540px;
  background-image: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/product-8.png");
  padding-right: 15px;
  text-decoration: none;
  font-size: 72px;
  line-height: 108px;
  color: #AA0601;
  text-align: center;
  padding: 180px 0px;
  margin: 0px 15px; }

.youth h2 a {
  display: block;
  max-width: 540px;
  background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/product-9.png?raw=true");
  background-size: cover;
  padding-left: 15px;
  text-decoration: none;
  font-size: 72px;
  line-height: 108px;
  color: #AA0601;
  text-align: center;
  padding: 180px 0px;
  margin: 0px 15px; }

.youth h2 a:hover, .double-a h2 a:hover {
  opacity: .6;
  color: #ffffff; }

@media (max-width: 992px) {
  .joint-name h3 {
    font-size: 32px;
    line-height: 48px; }
  .youth h2 a, .double-a h2 a {
    max-width: 330px;
    font-size: 32px;
    line-height: 25px; } }

@media (max-width: 767px) {
  .joint-name ul {
    flex-direction: column; }
  .joint-info li {
    width: 100%;
    margin-bottom: 16px; } }

/* 顧客推薦 */
.recommend h3 {
  font-size: 64px;
  line-height: 96px;
  text-align: center; }

.recommend ul {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.recommend-card {
  max-width: 23.3%;
  border: 1px solid #dee2e6;
  box-shadow: 0 2px #dee2e6;
  border-radius: 4px;
  margin-bottom: 16px; }

.recommend-info img {
  max-width: 100%;
  margin-bottom: 8px; }

.recommed-info h6 {
  font-size: 14px;
  font-weight: 21px;
  color: #212529;
  margin: 8px 16px; }

.recommed-info p {
  height: 72px;
  font-size: 16px;
  font-weight: 24px;
  color: #212529;
  margin: 8px 16px; }

.recommend-card .date {
  font-size: 14px;
  color: #707070;
  margin-top: 68px;
  margin-left: 16px; }

@media (max-width: 992px) {
  .recommend h3 {
    font-size: 32px;
    line-height: 48px; }
  .recommend-card {
    max-width: 48%;
    margin-bottom: 32px; } }

@media (max-width: 767px) {
  .recommend-card {
    max-width: 100%;
    margin-bottom: 16px; } }

/* 聯絡我們 */
.form {
  padding: 36px auto; }

.form-container {
  max-width: 60%;
  margin: 0px auto; }

.form h2 {
  font-size: 48px;
  font-weight: 72px;
  text-align: center;
  margin: 36px 0px; }

.form p {
  font-size: 20px;
  font-weight: 30px;
  margin-bottom: 20px; }

.form-group input, .form-group textarea {
  width: 100%;
  padding: 14px 16px;
  background: #ffffff;
  border: 2px solid #000;
  border-radius: 2%; }

.form-group .submit-btn {
  color: #ffffff;
  padding: 16px 143px;
  background: #AA0601;
  margin: 0 auto;
  border: 0px; }

.form-group .submit-btn:hover {
  padding: 22px 153px; }

/* 頁尾 */
.footer {
  padding-top: 32px;
  padding-bottom: 32px;
  color: #ffffff;
  font-size: 16px;
  line-height: 24px; }

.footer-info li a, .footer-icon li a {
  color: #ffffff;
  font-size: 16px;
  line-height: 24px;
  text-decoration: none;
  padding-right: 48px; }

.footer-icon li {
  padding-left: 32px; }

.footer i {
  display: block;
  padding: 16px 0px;
  font-size: 36px;
  line-height: 54px; }

.content-info {
  font-size: 36px;
  line-height: 54px; }

.statement {
  border-top: #ffffff 1px solid;
  padding-top: 32px; }

.statement-info li a {
  font-size: 14px;
  line-height: 21px;
  color: #ffffff;
  padding-left: 48px;
  text-decoration: none; }

@media (max-width: 992px) {
  .footer {
    padding-bottom: 21px; }
  .footer .footer-bar {
    margin-bottom: 18px; }
  .footer-info li a, .footer-icon li a {
    padding-right: 24px; }
  .footer-icon li {
    padding-left: 24px; }
  .footer-icon li:nth-child(1) {
    padding-left: 0px; }
  .footer i {
    display: block;
    padding: 6px 0px;
    font-size: 20px;
    line-height: 30px; } }

@media (max-width: 767px) {
  .footer-info {
    display: none; }
  .footer-icon li {
    padding-right: 24px; }
  .statement {
    display: block;
    padding-top: 20px; }
  .statement-info {
    display: block; }
  .statement-info li a {
    padding-left: 0px; } }

